@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
/* <----- Fonts */

/* <----- Basic  */
body {
	margin: 0;
	padding: 0;
	font-family: 'Open Sans', sans-serif;
}

nav {
	margin-bottom: 12px;
}

.card {
	margin-bottom: 10px;
	cursor: pointer;
}

/* <----- Colours */
body {
	background-color: #282c34 !important;
	color: #EDEDED !important;
}

nav {
	background-color: #1C1F24;
}

a {
	color: #EDEDED !important;
}

input[type=text],
input[type=number] {
	background-color: #14161A;
	color: #EDEDED;
}

input[type=text]:focus,
input[type=number]:focus {
	background-color: #1C1F24;
	color: #EDEDED;
}

.card {
	background-color: #1C1F24;
}

/* <----- Header */
.header, #search-form {
	padding: 20px;
}

/* <----- Body (posts, error, indicators) */
.loading, .error {
	padding-left: 20px;
}

.error {
	color: red;
}

#posts {
	line-height: 0;

	-webkit-column-count: 5;
	-webkit-column-gap: 0px;
	-moz-column-count: 5;
	-moz-column-gap: 0px;
	column-count: 5;
	grid-column-gap: 0px;
	column-gap: 0px;
}

#posts img {
	width: 100%;
	height: auto;
	cursor: pointer;
}

.video {
	border-color: blue;
	border-style: solid;
	border-width: 1px
}

.gif {
	border-color: orange;
	border-style: solid;
	border-width: 1px
}

.flash {
	border-color: red;
	border-style: solid;
	border-width: 1px
}

@media (max-width: 1200px) {
	#posts {
		-webkit-column-count: 4;
		column-count: 4;
	}
}
@media (max-width: 1000px) {
	#posts {
		-webkit-column-count: 3;
		column-count: 3;
	}
}
@media (max-width: 800px) {
	#posts {
		-webkit-column-count: 2;
		column-count: 2;
	}
}
@media (max-width: 400px) {
	#posts {
		-webkit-column-count: 1;
		column-count: 1;
	}
}

